body {
  background-color: #2a332e;
  color: #000000;
}
/* for desktop */
@media screen and (min-width: 992px) {
  .login_section {
    width: 43%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .btns_row {
    width: 70%;
    margin: 0 auto;
    text-align: center;
  }

  .middle_btn_row {
    width: 40% !important;
  }

  .float_right {
    float: right;
  }

  .choose_btns {
    margin-top: 20px;
  }
  .form {
    width: 60%;
    margin: 0 auto;
  }
}

/* for Mobile */

@media screen and (max-width: 992px) {
  .login_section {
    padding: 40px;
    max-width: 375px;
    margin: 0 auto;
  }

  .fs_18 {
    font-size: 18px !important;
  }

  .choose_btns {
    margin-top: 20px;
    text-align: center !important;
  }

  .btns_row {
    text-align: center;
  }

  .col-6 {
    text-align: center;
    padding: 0;
  }
}

.white-box {
  border-radius: 5px;
  padding: 15px;
}

.fs_12 {
  font-size: 12px;
}

.form input {
  background-color: #f27c89;
  color: #000 !important;
  font-size: 16px;
  outline: none;
  font-weight: 500;
  border: none;
}

.form-control:focus {
  color: #000000 !important;
  background-color: #f27c89 !important;
  border-color: none !important;
  outline: 0 !important;
  box-shadow: none !important;
}

.form-control::placeholder {
  color: #403b37;
  font-weight: 500;
}

.login_btn {
  background-color: white !important;
  color: #000000;
  font-weight: 600;
  outline: none;
  border: 0;
  font-size: 22px;
  padding: 8px 30px;
}

.choose_btns {
  padding: 6px 12px;
}

.login_btn:focus {
  box-shadow: none;
}

.pink_clr {
  background-color: #f27c89 !important;
}
